.rdforms-specs table.def > tbody > tr:last-child td {
  border-bottom: 0;
}
.rdforms-specs table.def th {
  font-style: italic;
  font-weight: normal;
  padding-left: 1em;
  width: 3em;
}
.rdforms-specs table td.pre {
  white-space: pre-wrap;
}
.rdforms-specs table.def td.footnote {
  padding-top: 0.6em;
}
.rdforms-specs table.def td.footnote::before {
  content: " ";
  display: block;
  height: 0.6em;
  width: 4em;
  border-top: thin solid;
}
.rdforms-specs table {
  word-wrap: normal;
  overflow-wrap: normal;
  hyphens: manual;
}
.rdforms-specs table.data,
.rdforms-specs table.index {
