  border-top: 1px solid silver;
  padding-right: 1em;
}
.rdforms-specs table.data th[colspan],
.rdforms-specs table.data td[colspan] {
  text-align: center;
}
.rdforms-specs table.complex.data th,
.rdforms-specs table.complex.data td {
  border: 1px solid silver;
  text-align: center;
}
.rdforms-specs table.data.longlastcol td:last-child,
.rdforms-specs table.data td.long {
  vertical-align: baseline;
  text-align: left;
}
.rdforms-specs table.data img {
  vertical-align: middle;
}
.rdforms-specs .toc a {
  /* More spacing; use padding to make it part of the click target. */
  padding-top: 0.1rem;
  /* Larger, more consistently-sized click target */
  display: block;
  /* Reverse color scheme */
  color: black;
  border-color: #0071BC;
}
.rdforms-specs .toc a:visited {
  border-color: #154862;
