    top: 0;
    bottom: 0;
    left: 0;
    width: 23.5em;
    max-width: 80%;
    max-width: calc(100% - 2em - 26px);
    overflow: auto;
    padding: 0 1em;
    padding-left: 42px;
    padding-left: calc(1em + 26px);
    background: inherit;
    background-color: #f7f8f9;
    z-index: 1;
    box-shadow: -0.1em 0 0.25em rgba(0, 0, 0, 0.1) inset;
