    box-shadow: -0.1em 0 0.25em rgba(0, 0, 0, 0.1) inset;
  }
  .rdforms-specs:not(.toc-inline) #toc h2 {
    margin-top: 0.8rem;
    font-variant: small-caps;
    font-variant: all-small-caps;
    text-transform: lowercase;
    font-weight: bold;
    color: #154862;
  }
  .rdforms-specs:not(.toc-inline) {
    padding-left: 29em;
  }
  /* See also Overflow section at the bottom */
  .rdforms-specs:not(.toc-inline) #toc-jump:not(:focus) {
    width: 0;
    height: 0;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }
}
