EF8kC9D;EE7kCD;IAA+D,iBAAA;EFglC9D;EE/kCD;IAA+D,kBAAA;EFklC9D;EEjlCD;IAA+D,kBAAA;EFolC9D;EEnlCD;IAA+D,kBAAA;EFslC9D;AACF;AEplCA;EACC;IFslCG,0CAA0C;IEplC5C,yCAAA;IACA,0CAAA;EFslCA;AACF;AEplCA;EACC;IFslCG,0DAA0D;IEplC5D,yBAAA;IACA,6CAAA;EFslCA;AACF;AA3uCA;EACE,oEAAA;AA6uCF;AA1uCA;EACE,YAAA;EACA,iBAAA;AA4uCF;AAzuCA;;EAEE,uBAAA;EACA,4BAAA;EACA,kBAAA;AA2uCF","sourcesContent":[".rdforms-specs {\n  @import (less) 'respec.css';\n}\n@import (less) \"respec-body.css\";\n\n.rdforms-specs {\n  background-image: url(margin-blue.png) !important;\n}\n\n.rdforms-specs .logo img {\n  height: 90px;\n  margin-top: -10px;\n}\n\n.rdforms-specs .ms_datatable table.data tbody td:first-child,\n.rdforms-specs .ms_datatable table.data thead th:first-child  {\n  border-right: 2px solid;\n  border-top: 1px solid silver;\n  padding-right: 1em;\n}","/******************************************************************************\n *                   Style sheet for the W3C specifications                   *\n *\n * Special classes handled by this style sheet include:\n *\n * Indices\n *   - .toc for the Table of Contents (<ol class=\"toc\">)\n *     + <span class=\"secno\"> for the section numbers\n *   - #toc for the Table of Contents (<nav id=\"toc\">)\n *   - ul.index for Indices (<a href=\"#ref\">term</a><span>, in N.M</span>)\n *   - table.index for Index Tables (e.g. for properties or elements)\n *\n * Structural Markup\n *   - table.data for general data tables\n *     -> use 'scope' attribute, <colgroup>, <thead>, and <tbody> for best results !\n *     -> use <table class='complex data'> for extra-complex tables\n *     -> use <td class='long'> for paragraph-length cell content\n *     -> use <td class='pre'> when manual line breaks/indentation would help readability\n *   - dl.switch for switch statements\n *   - ol.algorithm for algorithms (helps to visualize nesting)\n *   - .figure and .caption (HTML4) and figure and figcaption (HTML5)\n *     -> .sidefigure for right-floated figures\n *   - ins/del\n *\n * Code\n *   - pre and code\n *\n * Special Sections\n *   - .note       for informative notes             (div, p, span, aside, details)\n *   - .example    for informative examples          (div, p, pre, span)\n *   - .issue      for issues                        (div, p, span)\n *   - .advisement for loud normative statements     (div, p, strong)\n *   - .annoying-warning for spec obsoletion notices (div, aside, details)\n *\n * Definition Boxes\n *   - pre.def   for WebIDL definitions\n *   - table.def for tables that define other entities (e.g. CSS properties)\n *   - dl.def    for definition lists that define other entitles (e.g. HTML elements)\n *\n * Numbering\n *   - .secno for section numbers in .toc and headings (<span class='secno'>3.2</span>)\n *   - .marker for source-inserted example/figure/issue numbers (<span class='marker'>Issue 4</span>)\n *   - ::before styled for CSS-generated issue/example/figure numbers:\n *     -> Documents wishing to use this only need to add\n *        figcaption::before,\n *        .caption::before { content: \"Figure \"  counter(figure) \" \";  }\n *        .example::before { content: \"Example \" counter(example) \" \"; }\n *        .issue::before   { content: \"Issue \"   counter(issue) \" \";   }\n *\n * Header Stuff (ignore, just don't conflict with these classes)\n *   - .head for the header\n *   - .copyright for the copyright\n *\n * Outdated warning for old specs\n *\n * Miscellaneous\n *   - .overlarge for things that should be as wide as possible, even if\n *     that overflows the body text area. This can be used on an item or\n *     on its container, depending on the effect desired.\n *     Note that this styling basically doesn't help at all when printing,\n *     since A4 paper isn't much wider than the max-width here.\n *     It's better to design things to fit into a narrower measure if possible.\n *   - js-added ToC jump links (see tocControlls.js)\n *\n ******************************************************************************/\n\n/******************************************************************************/\n/*                         Front Matter & Navigation                          */\n/******************************************************************************/\n\n/** Header ********************************************************************/\n\n\tdiv.head { margin-bottom: 1em; }\n\tdiv.head hr { border-style: solid; }\n\n\tdiv.head h1 {\n\t\tfont-weight: bold;\n\t\tmargin: 0 0 .1em;\n\t\tfont-size: 220%;\n\t}\n\n\tdiv.head h2 { margin-bottom: 1.5em;}\n\n/** W3C Logo ******************************************************************/\n\n\t.head p:not(.copyright):first-child {\n\t\tmargin: 0;\n\t}\n\n\t.head p:not(.copyright):first-child > a,\n\t.head > a:first-child {\n\t\tfloat: right;\n\t\tmargin: 0.4rem 0 0.2rem .4rem;\n\t}\n\n\t.head img[src*=\"logos/W3C\"] {\n\t\tdisplay: block;\n\t\tborder: solid #1a5e9a;\n\t\tborder-width: .65rem .7rem .6rem;\n\t\tborder-radius: .4rem;\n\t\tbackground: #1a5e9a;\n\t\tcolor: white;\n\t\tfont-weight: bold;\n\t}\n\n\t.head a:hover > img[src*=\"logos/W3C\"],\n\t.head a:focus > img[src*=\"logos/W3C\"] {\n\t\topacity: .8;\n\t}\n\n\t.head a:active > img[src*=\"logos/W3C\"] {\n\t\tbackground: #c00;\n\t\tborder-color: #c00;\n\t}\n\n\t/* see also additional rules in Link Styling section */\n\n/** Copyright *****************************************************************/\n\n\tp.copyright,\n\tp.copyright small { font-size: small; }\n\n/** Back to Top / ToC Toggle **************************************************/\n\n\t@media print {\n\t\t#toc-nav {\n\t\t\tdisplay: none;\n\t\t}\n\t}\n\t@media not print {\n\t\t#toc-nav {\n\t\t\tposition: fixed;\n\t\t\tz-index: 2;\n\t\t\tbottom: 0; left: 0;\n\t\t\tmargin: 0;\n\t\t\tmin-width: 1.33em;\n\t\t\tborder-top-right-radius: 2rem;\n\t\t\tbox-shadow: 0 0 2px;\n\t\t\tfont-size: 1.5em;\n\t\t\tcolor: black;\n\t\t}\n\t\t#toc-nav > a {\n\t\t\tdisplay: block;\n\t\t\twhite-space: nowrap;\n\n\t\t\theight: 1.33em;\n\t\t\tpadding: .1em 0.3em;\n\t\t\tmargin: 0;\n\n\t\t\tbox-shadow: 0 0 2px;\n\t\t\tborder: none;\n\t\t\tborder-top-right-radius: 1.33em;\n\t\t\tbackground: white;\n\t\t}\n\t\t#toc-nav > #toc-jump {\n\t\t\tpadding-bottom: 2em;\n\t\t\tmargin-bottom: -1.9em;\n\t\t}\n\n\t\t#toc-nav > a:hover,\n\t\t#toc-nav > a:focus {\n\t\t\tbackground: #f8f8f8;\n\t\t}\n\t\t#toc-nav > a:not(:hover):not(:focus) {\n\t\t\tcolor: #707070;\n\t\t}\n\n\t\t/* statusbar gets in the way on keyboard focus; remove once browsers fix */\n\t\t#toc-nav > a[href=\"#toc\"]:not(:hover):focus:last-child {\n\t\t\tpadding-bottom: 1.5rem;\n\t\t}\n\n\t\t#toc-nav:not(:hover) > a:not(:focus) > span + span {\n\t\t\t/* Ideally this uses :focus-within on #toc-nav */\n\t\t\tdisplay: none;\n\t\t}\n\t\t#toc-nav > a > span + span {\n\t\t\tpadding-right: 0.2em;\n\t\t}\n\n\t\t#toc-toggle-inline {\n\t\t\tvertical-align: 0.05em;\n\t\t\tfont-size: 80%;\n\t\t\tcolor: gray;\n\t\t\tcolor: hsla(203,20%,40%,.7);\n\t\t\tborder-style: none;\n\t\t\tbackground: transparent;\n\t\t\tposition: relative;\n\t\t}\n\t\t#toc-toggle-inline:hover:not(:active),\n\t\t#toc-toggle-inline:focus:not(:active) {\n\t\t\ttext-shadow: 1px 1px silver;\n\t\t\ttop: -1px;\n\t\t\tleft: -1px;\n\t\t}\n\n\t\t#toc-nav :active {\n\t\t\tcolor: #C00;\n\t\t}\n\t}\n\n/** ToC Sidebar ***************************************************************/\n\n\n/******************************************************************************/\n/*                                Sectioning                                  */\n/******************************************************************************/\n\n/** Headings ******************************************************************/\n\n\th1, h2, h3, h4, h5, h6, dt {\n\t\tpage-break-after: avoid;\n\t\tpage-break-inside: avoid;\n\t\tfont: 100% sans-serif;   /* Reset all font styling to clear out UA styles */\n\t\tfont-family: inherit;    /* Inherit the font family. */\n\t\tline-height: 1.2;        /* Keep wrapped headings compact */\n\t\thyphens: manual;         /* Hyphenated headings look weird */\n\t}\n\n\th2, h3, h4, h5, h6 {\n\t\tmargin-top: 3rem;\n\t}\n\n\th1, h2, h3 {\n\t\tcolor: #154862;\n\t\tbackground: transparent;\n\t}\n\n\th1 { font-size: 170%; }\n\th2 { font-size: 140%; }\n\th3 { font-size: 120%; }\n\th4 { font-weight: bold; }\n\th5 { font-style: italic; }\n\th6 { font-variant: small-caps; }\n\tdt { font-weight: bold; }\n\n/** Subheadings ***************************************************************/\n\n\th1 + h2,\n\t#subtitle {\n\t\t/* #subtitle is a subtitle in an H2 under the H1 */\n\t\tmargin-top: 0;\n\t}\n\th2 + h3,\n\th3 + h4,\n\th4 + h5,\n\th5 + h6 {\n\t\tmargin-top: 1.2em; /* = 1 x line-height */\n\t}\n\n/** Section divider ***********************************************************/\n\n\t:not(.head) > :not(.head) + hr {\n\t\tfont-size: 1.5em;\n\t\ttext-align: center;\n\t\tmargin: 1em auto;\n\t\theight: auto;\n\t\tborder: transparent solid 0;\n\t\tbackground: transparent;\n\t}\n\t:not(.head) > hr::before {\n\t\tcontent: \"\\2727\\2003\\2003\\2727\\2003\\2003\\2727\";\n\t}\n\n/******************************************************************************/\n/*                            Paragraphs and Lists                            */\n/******************************************************************************/\n\n\tp {\n\t\tmargin: 1em 0;\n\t}\n\n\tdd > p:first-child,\n\tli > p:first-child {\n\t\tmargin-top: 0;\n\t}\n\n\tul, ol {\n\t\tmargin-left: 0;\n\t\tpadding-left: 2em;\n\t}\n\n\tli {\n\t\tmargin: 0.25em 0 0.5em;\n\t\tpadding: 0;\n\t}\n\n\tdl dd {\n\t\tmargin: 0 0 .5em 2em;\n\t}\n\n\t.head dd + dd { /* compact for header */\n\t\tmargin-top: -.5em;\n\t}\n\n\t/* Style for algorithms */\n\n\tol.algorithm ol:not(.algorithm),\n\t.algorithm > ol ol:not(.algorithm) {\n\t    border-left: 0.5em solid #DEF;\n\t}\n\n    /* Style for versions */\n    ul.versions {\n\t\tlist-style-type: none;\n\t}\n\tul.versions>li {\n\t\tfont-weight: bold;\n\t}\n    ul.versions ul {\n\t\tfont-weight: normal;\n\t\tborder-left: 0.5em solid #DEF;\n\t\tlist-style-type: none;\n\t}\n    ul.versions ul > li {\n\t\ttext-indent: -15px;\n\t}\n    ul.versions ul > li:before {\n\t\tcontent: \"-  \";\n\t\ttext-indent: -5px;\n\t}\n\n\t/* Style for switch/case <dl>s */\n\tdl.switch > dd > ol.only {\n\t margin-left: 0;\n\t}\n\tdl.switch > dd > ol.algorithm {\n\t margin-left: -2em;\n\t}\n\tdl.switch {\n\t padding-left: 2em;\n\t}\n\tdl.switch > dt {\n\t text-indent: -1.5em;\n\t margin-top: 1em;\n\t}\n\tdl.switch > dt + dt {\n\t margin-top: 0;\n\t}\n\tdl.switch > dt::before {\n\t content: '\\21AA';\n\t padding: 0 0.5em 0 0;\n\t display: inline-block;\n\t width: 1em;\n\t text-align: right;\n\t line-height: 0.5em;\n\t}\n\n/** Terminology Markup ********************************************************/\n\n\n/******************************************************************************/\n/*                                 Inline Markup                              */\n/******************************************************************************/\n\n/** Terminology Markup ********************************************************/\n\tdfn   { /* Defining instance */\n\t\tfont-weight: bolder;\n\t}\n\ta > i { /* Instance of term */\n\t\tfont-style: normal;\n\t}\n\tdt dfn code, code.idl {\n\t\tfont-size: inherit;\n\t}\n\tdfn var {\n\t\tfont-style: normal;\n\t}\n\n/** Change Marking ************************************************************/\n\n\tdel { color: red;  text-decoration: line-through; }\n\tins { color: #080; text-decoration: underline;    }\n\n/** Miscellaneous improvements to inline formatting ***************************/\n\n\tsup {\n\t\tvertical-align: super;\n\t\tfont-size: 80%;\n\t}\n\n/******************************************************************************/\n/*                                    Code                                    */\n/******************************************************************************/\n\n/** General monospace/pre rules ***********************************************/\n\n\tpre, code, samp {\n\t\tfont-family: Menlo, Consolas, \"DejaVu Sans Mono\", Monaco, monospace;\n\t\tfont-size: .9em;\n\t\tpage-break-inside: avoid;\n\t\thyphens: none;\n\t\ttext-transform: none;\n\t\ttext-align: left;\n\t\ttext-align: start;\n\t}\n\tpre code,\n\tcode code {\n\t\tfont-size: 100%;\n\t}\n\n\tpre {\n\t\tmargin-top: 1em;\n\t\tmargin-bottom: 1em;\n\t\toverflow: auto;\n\t}\n\n/** Inline Code fragments *****************************************************/\n\n  /* Do something nice. */\n\n/******************************************************************************/\n/*                                    Links                                   */\n/******************************************************************************/\n\n/** General Hyperlinks ********************************************************/\n\n\t/* We hyperlink a lot, so make it less intrusive */\n\ta[href] {\n\t\tcolor: #005A9C; /*MP WCAG2.1 contrast adaption, old value: #0071BC;*/\n\t\ttext-decoration: none;\n\t\tborder-bottom: 1px solid #0071BC;\n\t\t/* Need a bit of extending for it to look okay */\n\t\tpadding: 0 1px 0;\n\t\tmargin: 0 -1px 0;\n\t}\n\ta:visited {\n\t\tborder-bottom-color: #BBB;\n\t}\n\n\t/* Use distinguishing colors when user is interacting with the link */\n\ta[href]:focus,\n\ta[href]:hover {\n\t\tbackground: #f8f8f8;\n\t\tbackground: rgba(75%, 75%, 75%, .25);\n\t\tborder-bottom-width: 3px;\n\t\tmargin-bottom: -2px;\n\t}\n\ta[href]:active {\n\t\tcolor: #C00;\n\t\tborder-color: #C00;\n\t}\n\n\t/* Backout above styling for W3C logo */\n\t.head p:not(.copyright) > a,\n\t.head > a:first-child {\n\t\tborder: none;\n\t\ttext-decoration: none;\n\t\tbackground: transparent;\n\t}\n\n/******************************************************************************/\n/*                                    Images                                  */\n/******************************************************************************/\n\n\timg {\n\t\tborder-style: none;\n\t}\n\n\t/* For autogen numbers, add\n\t   .caption::before, figcaption::before { content: \"Figure \" counter(figure) \". \"; }\n\t*/\n\n\tfigure, .figure, .sidefigure {\n\t\tpage-break-inside: avoid;\n\t\ttext-align: center;\n\t\tmargin: 2.5em 0;\n\t}\n\t.figure img,    .sidefigure img,    figure img,\n\t.figure object, .sidefigure object, figure object {\n\t\tmax-width: 100%;\n\t\tmargin: auto;\n\t\theight: auto;\n\t}\n\t.figure pre, .sidefigure pre, figure pre {\n\t\ttext-align: left;\n\t\tdisplay: table;\n\t\tmargin: 1em auto;\n\t}\n\t.figure table, figure table {\n\t\tmargin: auto;\n\t}\n\t@media screen and (min-width: 20em) {\n\t\t.sidefigure {\n\t\t\tfloat: right;\n\t\t\twidth: 50%;\n\t\t\tmargin: 0 0 0.5em 0.5em;\n\t\t}\n\t}\n\t.caption, figcaption, caption {\n\t\tfont-style: italic;\n\t\tfont-size: 90%;\n\t}\n\t.caption::before, figcaption::before, figcaption > .marker {\n\t\tfont-weight: bold;\n\t}\n\t.caption, figcaption {\n\t\tcounter-increment: figure;\n\t}\n\n\t/* DL list is indented 2em, but figure inside it is not */\n\tdd > .figure, dd > figure { margin-left: -2em; }\n\n/******************************************************************************/\n/*                             Colored Boxes                                  */\n/******************************************************************************/\n\n\t.issue, .note, .example, .advisement, blockquote {\n\t\tpadding: .5em;\n\t\tborder: .5em;\n\t\tborder-left-style: solid;\n\t\tpage-break-inside: avoid;\n\t}\n\tspan.issue, span.note {\n\t\tpadding: .1em .5em .15em;\n\t\tborder-right-style: solid;\n\t}\n\n\t.issue,\n\t.note,\n\t.example,\n\t.advisement,\n\tblockquote {\n\t\tmargin: 1em auto;\n\t}\n\t.note  > p:first-child,\n\t.issue > p:first-child,\n\tblockquote > :first-child {\n\t\tmargin-top: 0;\n\t}\n\tblockquote > :last-child {\n\t\tmargin-bottom: 0;\n\t}\n\n/** Blockquotes ***************************************************************/\n\n\tblockquote {\n\t\tborder-color: silver;\n\t}\n\n/** Open issue ****************************************************************/\n\n\t.issue {\n\t\tborder-color: #E05252;\n\t\tbackground: #FBE9E9;\n\t\tcounter-increment: issue;\n\t\toverflow: auto;\n\t}\n\t.issue::before, .issue > .marker {\n\t\tcolor: #AE1E1E;\n\t\tpadding-right: 1em;\n\t\ttext-transform: uppercase;\n\t}\n\t/* Add .issue::before { content: \"Issue \" counter(issue) \" \"; } for autogen numbers,\n\t   or use class=\"marker\" to mark up the issue number in source. */\n\n/** Example *******************************************************************/\n\n\t.example {\n\t\tborder-color: #E0CB52;\n\t\tbackground: #FCFAEE;\n\t\tcounter-increment: example;\n\t\toverflow: auto;\n\t\tclear: both;\n\t}\n\t.example::before, .example > .marker {\n\t\ttext-transform: uppercase;\n\t\tcolor: #827017;\n\t\tmin-width: 7.5em;\n\t\tdisplay: block;\n\t}\n\t/* Add .example::before { content: \"Example \" counter(example) \" \"; } for autogen numbers,\n\t   or use class=\"marker\" to mark up the example number in source. */\n\n/** Non-normative Note ********************************************************/\n\n\t.note {\n\t\tborder-color: #52E052;\n\t\tbackground: #E9FBE9;\n\t\toverflow: auto;\n\t}\n\n\t.note::before, .note > .marker,\n\tdetails.note > summary::before,\n\tdetails.note > summary > .marker {\n\t\ttext-transform: uppercase;\n\t\tdisplay: block;\n\t\tcolor: hsl(120, 70%, 30%);\n\t}\n\t/* Add .note::before { content: \"Note \"; } for autogen label,\n\t   or use class=\"marker\" to mark up the label in source. */\n\n\tdetails.note > summary {\n\t\tcolor: hsl(120, 70%, 30%);\n\t}\n\tdetails.note[open] > summary {\n\t\tborder-bottom: 1px silver solid;\n\t}\n\n/** Advisement Box ************************************************************/\n\t/*  for attention-grabbing normative statements */\n\n\t.advisement {\n\t\tborder-color: orange;\n\t\tborder-style: none solid;\n\t\tbackground: #FFEECC;\n\t}\n\tstrong.advisement {\n\t\tdisplay: block;\n\t\ttext-align: center;\n\t}\n\t.advisement > .marker {\n\t\tcolor: #B35F00;\n\t}\n\n/** Spec Obsoletion Notice ****************************************************/\n\t/* obnoxious obsoletion notice for older/abandoned specs. */\n\n\tdetails {\n\t\tdisplay: block;\n\t}\n\tsummary {\n\t\tfont-weight: bolder;\n\t}\n\n\t.annoying-warning:not(details),\n\tdetails.annoying-warning:not([open]) > summary,\n\tdetails.annoying-warning[open] {\n\t\tbackground: hsla(40,100%,50%,0.95);\n\t\tcolor: black;\n\t\tpadding: .75em 1em;\n\t\tborder: red;\n\t\tborder-style: solid none;\n\t\tbox-shadow: 0 2px 8px black;\n\t\ttext-align: center;\n\t}\n\t.annoying-warning :last-child {\n\t\tmargin-bottom: 0;\n\t}\n\n@media not print {\n\tdetails.annoying-warning[open] {\n\t\tposition: fixed;\n\t\tleft: 0;\n\t\tright: 0;\n\t\tbottom: 2em;\n\t\tz-index: 1000;\n\t}\n}\n\n\tdetails.annoying-warning:not([open]) > summary {\n\t\ttext-align: center;\n\t}\n\n/** Entity Definition Boxes ***************************************************/\n\n\t.def {\n\t\tpadding: .5em 1em;\n\t\tbackground: #DEF;\n\t\tmargin: 1.2em 0;\n\t\tborder-left: 0.5em solid #8CCBF2;\n\t}\n\n/******************************************************************************/\n/*                                    Tables                                  */\n/******************************************************************************/\n\n\tth, td {\n\t\ttext-align: left;\n\t\ttext-align: start;\n\t}\n\n/** Property/Descriptor Definition Tables *************************************/\n\n\ttable.def {\n\t\t/* inherits .def box styling, see above */\n\t\twidth: 100%;\n\t\tborder-spacing: 0;\n\t}\n\n\ttable.def td,\n\ttable.def th {\n\t\tpadding: 0.5em;\n\t\tvertical-align: baseline;\n\t\tborder-bottom: 1px solid #bbd7e9;\n\t}\n\n\ttable.def > tbody > tr:last-child th,\n\ttable.def > tbody > tr:last-child td {\n\t\tborder-bottom: 0;\n\t}\n\n\ttable.def th {\n\t\tfont-style: italic;\n\t\tfont-weight: normal;\n\t\tpadding-left: 1em;\n\t\twidth: 3em;\n\t}\n\n\t/* For when values are extra-complex and need formatting for readability */\n\ttable td.pre {\n\t\twhite-space: pre-wrap;\n\t}\n\n\t/* A footnote at the bottom of a def table */\n\ttable.def           td.footnote {\n\t\tpadding-top: 0.6em;\n\t}\n\ttable.def           td.footnote::before {\n\t\tcontent: \" \";\n\t\tdisplay: block;\n\t\theight: 0.6em;\n\t\twidth: 4em;\n\t\tborder-top: thin solid;\n\t}\n\n/** Data tables (and properly marked-up index tables) *************************/\n\t/*\n\t\t <table class=\"data\"> highlights structural relationships in a table\n\t\t when correct markup is used (e.g. thead/tbody, th vs. td, scope attribute)\n\n\t\t Use class=\"complex data\" for particularly complicated tables --\n\t\t (This will draw more lines: busier, but clearer.)\n\n\t\t Use class=\"long\" on table cells with paragraph-like contents\n\t\t (This will adjust text alignment accordingly.)\n\t\t Alternately use class=\"longlastcol\" on tables, to have the last column assume \"long\".\n\t*/\n\n\ttable {\n\t\tword-wrap: normal;\n\t\toverflow-wrap: normal;\n\t\thyphens: manual;\n\t}\n\n\ttable.data,\n\ttable.index {\n\t\tmargin: 1em auto;\n\t\tborder-collapse: collapse;\n\t\tborder: hidden;\n\t\twidth: 100%;\n\t}\n\ttable.data caption,\n\ttable.index caption {\n\t\tmax-width: 50em;\n\t\tmargin: 0 auto 1em;\n\t}\n\n\ttable.data td,  table.data th,\n\ttable.index td, table.index th {\n\t\tpadding: 0.5em 1em;\n\t\tborder-width: 1px;\n\t\tborder-color: silver;\n\t\tborder-top-style: solid;\n\t}\n\n\ttable.data thead td:empty {\n\t\tpadding: 0;\n\t\tborder: 0;\n\t}\n\n\ttable.data  thead,\n\ttable.index thead,\n\ttable.data  tbody,\n\ttable.index tbody {\n\t\tborder-bottom: 2px solid;\n\t}\n\n\ttable.data colgroup,\n\ttable.index colgroup {\n\t\tborder-left: 2px solid;\n\t}\n\n\ttable.data  tbody th:first-child,\n\ttable.index tbody th:first-child  {\n\t\tborder-right: 2px solid;\n\t\tborder-top: 1px solid silver;\n\t\tpadding-right: 1em;\n\t}\n\n\ttable.data th[colspan],\n\ttable.data td[colspan] {\n\t\ttext-align: center;\n\t}\n\n\ttable.complex.data th,\n\ttable.complex.data td {\n\t\tborder: 1px solid silver;\n\t\ttext-align: center;\n\t}\n\n\ttable.data.longlastcol td:last-child,\n\ttable.data td.long {\n\t vertical-align: baseline;\n\t text-align: left;\n\t}\n\n\ttable.data img {\n\t\tvertical-align: middle;\n\t}\n\n\n/*\nAlternate table alignment rules\n\n\ttable.data,\n\ttable.index {\n\t\ttext-align: center;\n\t}\n\n\ttable.data  thead th[scope=\"row\"],\n\ttable.index thead th[scope=\"row\"] {\n\t\ttext-align: right;\n\t}\n\n\ttable.data  tbody th:first-child,\n\ttable.index tbody th:first-child  {\n\t\ttext-align: right;\n\t}\n\nPossible extra rowspan handling\n\n\ttable.data  tbody th[rowspan]:not([rowspan='1']),\n\ttable.index tbody th[rowspan]:not([rowspan='1']),\n\ttable.data  tbody td[rowspan]:not([rowspan='1']),\n\ttable.index tbody td[rowspan]:not([rowspan='1']) {\n\t\tborder-left: 1px solid silver;\n\t}\n\n\ttable.data  tbody th[rowspan]:first-child,\n\ttable.index tbody th[rowspan]:first-child,\n\ttable.data  tbody td[rowspan]:first-child,\n\ttable.index tbody td[rowspan]:first-child{\n\t\tborder-left: 0;\n\t\tborder-right: 1px solid silver;\n\t}\n*/\n\n/******************************************************************************/\n/*                                  Indices                                   */\n/******************************************************************************/\n\n\n/** Table of Contents *********************************************************/\n\n\t.toc a {\n\t\t/* More spacing; use padding to make it part of the click target. */\n\t\tpadding-top: 0.1rem;\n\t\t/* Larger, more consistently-sized click target */\n\t\tdisplay: block;\n\t\t/* Reverse color scheme */\n\t\tcolor: black;\n\t\tborder-color: #0071BC;\n\t}\n\t.toc a:visited {\n\t\tborder-color: #154862;\n\t}\n\t.toc a:not(:focus):not(:hover) {\n\t\t/* Allow colors to cascade through from link styling */\n\t\tborder-bottom-color: transparent;\n\t}\n\n\t.toc, .toc ol, .toc ul, .toc li {\n\t\tlist-style: none; /* Numbers must be inlined into source */\n\t\t/* because generated content isn't search/selectable and markers can't do multilevel yet */\n\t\tmargin:  0;\n\t\tpadding: 0;\n\t\tline-height: 1.1rem; /* consistent spacing */\n\t}\n\n\t/* ToC not indented until third level, but font style & margins show hierarchy */\n\t.toc > li             { font-weight: bold;   }\n\t.toc > li li          { font-weight: normal; }\n\t.toc > li li li       { font-size:   95%;    }\n\t.toc > li li li li    { font-size:   90%;    }\n\t.toc > li li li li li { font-size:   85%;    }\n\n\t.toc > li             { margin: 1.5rem 0;    }\n\t.toc > li li          { margin: 0.3rem 0;    }\n\t.toc > li li li       { margin-left: 2rem;   }\n\n\t/* Section numbers in a column of their own */\n\t.toc .secno {\n\t\tfloat: left;\n\t\twidth: 4rem;\n\t\twhite-space: nowrap;\n\t}\n\t.toc > li li li li .secno {\n\t\tfont-size: 85%;\n\t}\n\t.toc > li li li li li .secno {\n\t\tfont-size: 100%;\n\t}\n\n\t:not(li) > .toc              { margin-left:  5rem; }\n\t.toc .secno                  { margin-left: -5rem; }\n\t.toc > li li li .secno       { margin-left: -7rem; }\n\t.toc > li li li li .secno    { margin-left: -9rem; }\n\t.toc > li li li li li .secno { margin-left: -11rem; }\n\n\t/* Tighten up indentation in narrow ToCs */\n\t@media (max-width: 30em) {\n\t\t:not(li) > .toc              { margin-left:  4rem; }\n\t\t.toc .secno                  { margin-left: -4rem; }\n\t\t.toc > li li li              { margin-left:  1rem; }\n\t\t.toc > li li li .secno       { margin-left: -5rem; }\n\t\t.toc > li li li li .secno    { margin-left: -6rem; }\n\t\t.toc > li li li li li .secno { margin-left: -7rem; }\n\t}\n\n\t.toc li {\n\t\tclear: both;\n\t}\n\n\n/** Index *********************************************************************/\n\n\t/* Index Lists: Layout */\n\tul.index       { margin-left: 0; columns: 15em; text-indent: 1em hanging; }\n\tul.index li    { margin-left: 0; list-style: none; break-inside: avoid; }\n\tul.index li li { margin-left: 1em; }\n\tul.index dl    { margin-top: 0; }\n\tul.index dt    { margin: .2em 0 .2em 20px;}\n\tul.index dd    { margin: .2em 0 .2em 40px;}\n\t/* Index Lists: Typography */\n\tul.index ul,\n\tul.index dl { font-size: smaller; }\n\t@media not print {\n\t\tul.index li span {\n\t\t\twhite-space: nowrap;\n\t\t\tcolor: transparent;\n\t\t}\n\t\tul.index li a:hover + span,\n\t\tul.index li a:focus + span {\n\t\t\tcolor: #707070;\n\t\t}\n\t}\n\n/** Index Tables *****************************************************/\n\t/* See also the data table styling section, which this effectively subclasses */\n\n\ttable.index {\n\t\tfont-size: small;\n\t\tborder-collapse: collapse;\n\t\tborder-spacing: 0;\n\t\ttext-align: left;\n\t\tmargin: 1em 0;\n\t}\n\n\ttable.index td,\n\ttable.index th {\n\t\tpadding: 0.4em;\n\t}\n\n\ttable.index tr:hover td:not([rowspan]),\n\ttable.index tr:hover th:not([rowspan]) {\n\t\tbackground: #f7f8f9;\n\t}\n\n\t/* The link in the first column in the property table (formerly a TD) */\n\ttable.index th:first-child a {\n\t\tfont-weight: bold;\n\t}\n\n/** Outdated warning **********************************************************/\n\na#outdated-note {\n\tcolor: white;\n}\n\na#outdated-note:hover {\n\tbackground: transparent;\n}\n\n.outdated-spec {\n\tbackground-color: rgba(0,0,0,0.5);\n}\n\n.outdated-warning {\n\tposition: fixed;\n\tbottom: 50%;\n\tleft: 0;\n\tright: 0;\n\tmargin: 0 auto;\n\twidth: 50%;\n\tbackground: maroon;\n\tcolor: white;\n\tborder-radius: 1em;\n\tbox-shadow: 0 0 1em red;\n\tpadding: 2em;\n\ttext-align: center;\n\tz-index: 2;\n}\n\n.edited-rec-warning {\n\tbackground: darkorange;\n\tbox-shadow: 0 0 1em;\n}\n\n.outdated-warning button {\n\tposition: absolute;\n\ttop: 0;\n\tright:0;\n\tmargin: 0;\n\tborder: 0;\n\tpadding: 0.25em 0.5em;\n\tbackground: transparent;\n\tcolor: white;\n\tfont:1em sans-serif;\n\ttext-align:center;\n}\n\n.outdated-warning span {\n\tdisplay: block;\n}\n\n.outdated-collapsed {\n\tbottom: 0;\n\tborder-radius: 0;\n\twidth: 100%;\n\tpadding: 0;\n}\n\n/******************************************************************************/\n/*                                    Print                                   */\n/******************************************************************************/\n\n\t@media print {\n\t\t/* Pages have their own margins. */\n\t\thtml {\n\t\t\tmargin: 0;\n\t\t}\n\n\t\t.outdated-warning {\n\t\t\tposition: absolute;\n\t\t\tborder-style: solid;\n\t\t\tborder-color: red;\n\t\t}\n\n\t\t.outdated-warning input {\n\t\t\tdisplay: none;\n\t\t}\n\t}\n\t@page {\n\t\tmargin: 1.5cm 1.1cm;\n\t}\n\n/******************************************************************************/\n/*                                    Legacy                                  */\n/******************************************************************************/\n\n\t/* This rule is inherited from past style sheets. No idea what it's for. */\n\t.hide { display: none; }\n\n\n\n/******************************************************************************/\n/*                             Overflow Control                               */\n/******************************************************************************/\n\n\t.figure .caption, .sidefigure .caption, figcaption {\n\t\t/* in case figure is overlarge, limit caption to 50em */\n\t\tmax-width: 50rem;\n\t\tmargin-left: auto;\n\t\tmargin-right: auto;\n\t}\n\t.overlarge > table {\n\t\t/* limit preferred width of table */\n\t\tmax-width: 50em;\n\t\tmargin-left: auto;\n\t\tmargin-right: auto;\n\t}\n\n\t@media (min-width: 55em) {\n\t\t.overlarge {\n\t\t\tmargin-left: calc(13px + 26.5rem - 50vw);\n\t\t\tmargin-right: calc(13px + 26.5rem - 50vw);\n\t\t\tmax-width: none;\n\t\t}\n\t}\n\n\t@media not print {\n\t\t.overlarge {\n\t\t\toverflow-x: auto;\n\t\t\t/* See Lea Verou's explanation background-attachment:\n\t\t\t * http://lea.verou.me/2012/04/background-attachment-local/\n\t\t\t *\n\t\t\tbackground: top left  / 4em 100% linear-gradient(to right,  #ffffff, rgba(255, 255, 255, 0)) local,\n\t\t\t            top right / 4em 100% linear-gradient(to left, #ffffff, rgba(255, 255, 255, 0)) local,\n\t\t\t            top left  / 1em 100% linear-gradient(to right,  #c3c3c5, rgba(195, 195, 197, 0)) scroll,\n\t\t\t            top right / 1em 100% linear-gradient(to left, #c3c3c5, rgba(195, 195, 197, 0)) scroll,\n\t\t\t            white;\n\t\t\tbackground-repeat: no-repeat;\n\t\t\t*/\n\t\t}\n\t}\n","/******************************************************************************/\n/*                                   Body                                     */\n/******************************************************************************/\n\n\t.rdforms-specs {\n\t\tcounter-reset: example figure issue;\n\n\t\t/* Layout */\n\t\tmax-width: 50em;               /* limit line length to 50em for readability   */\n\t\tmargin: 0 auto;                /* center text within page                     */\n\t\tpadding: 1.6em 1.5em 2em 50px; /* assume 16px font size for downlevel clients */\n\t\tpadding: 1.6em 1.5em 2em calc(26px + 1.5em); /* leave space for status flag     */\n\n\t\t/* Typography */\n\t\tline-height: 1.5;\n\t\tfont-family: sans-serif;\n\t\twidows: 2;\n\t\torphans: 2;\n\t\tword-wrap: break-word;\n\t\toverflow-wrap: break-word;\n\n\t\t/* Colors */\n\t\tcolor: black;\n\t\tbackground: white top left fixed no-repeat;\n\t\tbackground-size: 25px auto;\n\t}\n\n\n/******************************************************************************/\n/*                                    Print                                   */\n/******************************************************************************/\n\n@media print {\n\t/* Serif for print. */\n\t.rdforms-specs {\n\t\tfont-family: serif;\n\t}\n}\n\n/* Flo