specs:not(.toc-inline) {\n\t\tmargin: 0 4em;\n\t}\n}\n\n\n.rdforms-specs.toc-sidebar #toc :not(li) > .toc              { margin-left:  4rem; }\n.rdforms-specs.toc-sidebar #toc .toc .secno                  { margin-left: -4rem; }\n.rdforms-specs.toc-sidebar #toc .toc > li li li              { margin-left:  1rem; }\n.rdforms-specs.toc-sidebar #toc .toc > li li li .secno       { margin-left: -5rem; }\n.rdforms-specs.toc-sidebar #toc .toc > li li li li .secno    { margin-left: -6rem; }\n.rdforms-specs.toc-sidebar #toc .toc > li li li li li .secno { margin-left: -7rem; }\n\n@media screen and (min-width: 78em) {\n\t.rdforms-specs:not(.toc-inline) :not(li) > .toc              { margin-left:  4rem; }\n\t.rdforms-specs:not(.toc-inline) .toc .secno                  { margin-left: -4rem; }\n\t.rdforms-specs:not(.toc-inline) .toc > li li li              { margin-left:  1rem; }\n\t.rdforms-specs:not(.toc-inline) .toc > li li li .secno       { margin-left: -5rem; }\n\t.rdforms-specs:not(.toc-inline) .toc > li li li li .secno    { margin-left: -6rem; }\n\t.rdforms-specs:not(.toc-inline) .toc > li li li li li .secno { margin-left: -7rem; }\n}\n\n@media screen and (min-width: 78em) {\n\t.rdforms-specs:not(.toc-inline) .overlarge {\n\t\t/* 30.5em body padding 50em content area */\n\t\tmargin-left: calc(40em - 50vw) !important;\n\t\tmargin-right: calc(40em - 50vw) !important;\n\t}\n}\n@media screen and (min-width: 90em) {\n\t.rdforms-specs:not(.toc-inline) .overlarge {\n\t\t/* 4em html margin 30.5em body padding 50em content area */\n\t\tmargin-left: 0 !important;\n\t\tmargin-right: calc(84.5em - 100vw) !important;\n\t}\n}"],"sourceRoot":""}]);
